// IMAGE
%imageResMaxWidth {
  display: block;
  max-width: 100%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}
%imageResCoverAbs {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
// IMAGE :: end


// FONT FAMILY
%fontLato {
	font-family: 'Lato', sans-serif;
}
%fontOpenSans {
	font-family: 'Open Sans', sans-serif;
}
%fontPoppins {
	font-family: 'Poppins', sans-serif;
}
%fontSourceSansPro {
	font-family: 'Source Sans Pro', sans-serif;
}
// FONT FAMILY :: end
